import React, { useEffect } from "react";
import styled from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import Fish from "../components/Fish";
import { Button, Text, Grid, Container } from "../elements/";
import { KakaoImgUrl, instaImgUrl, copyLinkImgUrl, restartBtnImg, GrrrLinkBtnImg, DarkImg } from "../data/images/sharedImgs";
import "../shared/theme";
import { Spin } from "antd";
import { dic } from "../data/questionsFB";
import { api as resultActions } from "../redux-toolkit/modules/fishList";

const Result = (props) => {
  const dispatch = useDispatch();
  const _name = props.match.params.fishname;
  const history = props.history;
  console.log(props.match);

  const fish_result = useSelector((state) => state.fishList.onefish_result);
  const is_loaded = useSelector((state) => state.fishList.is_loaded);

  //       
  //   if   "fish"    Result    ,  "type"     mbti   .
  // ,      "fish"      useEffect     
  // ,   "fish"       "type"   mbti dispatch  !
  //    (,  ..?)            .

  useEffect(() => {
    // if (!sessionStorage.getItem("fish")) {
    //   dispatch(resultActions.getOneFishFB(sessionStorage.getItem("type")));
    // }
    dispatch(resultActions.getOneFishFB(_name));
  }, [_name]);

  if (sessionStorage.getItem("fish")) {
    sessionStorage.removeItem("fish");
    sessionStorage.setItem("type", fish_result?.type);
  }

  //  URL - /  usrParam
  const domain = "";
  const share_url = `${domain}${props.match.url}`;

  const copyToClipboard = () => {
    let t = document.createElement("textarea");
    t.value = share_url;
    document.body.appendChild(t);
    t.select();
    document.execCommand("copy");
    document.body.removeChild(t);
    window.alert(" !");
  };

  // Result page   ,  "  " ,
  //         !
  //     !

  const goBackToMain = () => {
    sessionStorage.setItem("goBack", true);
    window.location.href = "/";
  };

  // [ ]
  // https://developers.kakao.com/docs/latest/ko/getting-started/sdk-js#init
  // https://developers.kakao.com/docs/latest/ko/message/common
  // 1. index.html script .
  // 2.  Kakao.init(javascript key)
  // 3.      ,
  //       sendScrap  requestUrl    url .
  // useEffect(() => {
  //   // SDK  Kakao.init~  window    
  //   window.Kakao.init("e31c489577057b521747d2d2be3ce3d5");
  //   //  SDK    => console.log  true   
  //   // window.Kakao.isInitialized();
  // }, []);

  // const grrrDomain = "http://localhost:3000";
  // const _grrrDomain = "https://silver0r.tistory.com/57";

  // const sendLink = () => {
  //   window.Kakao.Link.sendScrap({
  //     requestUrl: `${grrrDomain}/result/`,
  //   });
  // };

  return (
    <Container>
      {is_loaded ? (
        <SpinWrap>
          <Spin />
        </SpinWrap>
      ) : Object.values(fish_result).length > 0 ? (
        <ResultContainer>
          <Fish OneFishType={fish_result} history={history} />
          <Share>
            <ShareTitle>
              <PurpleLeft src={require("../data/images/nomargin_third_left.png").default} />
              {/* <Button onClick={sendLink} color="yellow">blala</Button>*/}
              <Text bold size="1.5em" color="#00d0e9">
                
              </Text>
              <PurpleRight src={require("../data/images/nomargin_third_right.png").default} />
            </ShareTitle>
            <ShareChannel>
              <ShareChanBtn imgUrl={KakaoImgUrl} />
              <ShareChanBtn imgUrl={instaImgUrl} />
              <ShareChanBtn imgUrl={copyLinkImgUrl} onClick={copyToClipboard} />
            </ShareChannel>
          </Share>
          <Bottom>
            <RestartBtn onClick={goBackToMain} imgUrl={restartBtnImg} />
            <GrrrLinkBtn as={"a"} target="blank" rel="noreferrer noopener" href="https://www.youtube.com/channel/UCGrAnVVgQY66l9XHIzPxQEw" imgUrl={GrrrLinkBtnImg} />
          </Bottom>
          {/* <a target="blank" id="sns_facebook" href={`http://www.facebook.com/share.php?u=${_grrrDomain}&t=!`} title="   ">
          <Button round color="blue">
            F
          </Button>
        </a> */}
        </ResultContainer>
      ) : (
        <NoData>
          <Button onClick={() => (window.location.href = "/")}> </Button>
        </NoData>
      )}
    </Container>
  );
};

const ResultContainer = styled.div`
  background-image: url(${DarkImg});
  background-size: cover;
  background-position: center;
  width: 90vw;
  max-width: 500px;
  height: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  //border:1px dashed pink;

  @media ${(props) => props.theme.tablet} {
    width: 100%;
  }
  @media (max-width: 1024px) {
    width: 100%;
    max-width: 500px;
  }
  @media (max-width: 768px) {
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
  }
  @media (max-width: 720px) {
    width: 100%;
    /* max-width:768px; */
    margin: 0 auto;
  }
  @media (max-width: 420px) {
    width: 100%;
    max-width: 420px;
  }
  @media (max-width: 330px) {
    width: 100%;
    //max-width: 330px;
  }
`;

const PurpleLeft = styled.img`
  width: 10vw;
  max-width: 150px;
  height: 3vh;
  margin-right: 8px;
  @media (max-width: 720px) {
    width: 22vw;
  }
  @media (max-width: 420px) {
    width: 22vw;
  }
  /* @media (max-width: 330px) { 
    max-width: 400px;
} */
`;

const PurpleRight = styled.img`
  width: 10vw;
  max-width: 150px;
  height: 3vh;
  margin-left: 8px;
  @media (max-width: 720px) {
    width: 22vw;
  }
  @media (max-width: 420px) {
    width: 22vw;
  }
`;

const Share = styled.div`
  border: none;
`;

const ShareTitle = styled.div`
  width: 80vw;
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
`;

//sns
const ShareChannel = styled.div`
  width: 80vw;
  max-width: 400px;
  margin: 10px auto;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
`;

//sns
const ShareChanBtn = styled.button`
  cursor: pointer;
  width: 60px;
  height: 60px;
  margin: 20px 20px 0;
  border: none;
  background: no-repeat center/100% url(${(props) => props.imgUrl});
`;

const Bottom = styled.div`
  width: 100vw;
  max-width: 430px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding-bottom: 20px;
`;

const RestartBtn = styled.button`
  cursor: pointer;
  width: 55vw;
  height: 15vh;
  max-width: 200px;
  margin: 0 auto;
  border: none;
  background: no-repeat center/100% url(${(props) => props.imgUrl});
`;

const GrrrLinkBtn = styled.button`
  width: 100px;
  max-width: 200px;
  margin: 0.5vh auto;
  height: 100px;
  border: none;
  background: no-repeat center/100% url(${(props) => props.imgUrl});
`;

const NoData = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const SpinWrap = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default Result;
